<template>
  <Navbar></Navbar>
  <ToastList></ToastList>
  <Loading :active="isLoading"></Loading>
  <div class="shoppingGroup container">
    <div v-if="currentStep == 1" class="shopping-cart">
      <h3 class="cart-title">購物車</h3>
      <div v-if="cartItems.length === 0" class="empty-cart">
        <p>購物車為空</p>
      </div>
      <div v-else>
        <div class="cart-itemsGroup">
          <div class="cart-item" v-for="(item, key) in cartItems" :key="key">
            <img :src="item.product.imageUrl" alt="Product Image" class="item-image" />
            <div class="item-details">
              <h3>{{ item.product.title }}</h3>
              <p>{{ item.product.description }}</p>
              <div class="item-actions">
                <div class="quantity-control">
                  <button @click="countBack(item)">&#8722;</button>
                  <input
                    type="text"
                    v-model.number="item.qty"
                    @change="changeCount(item)"
                    class="quantity-input"
                  />
                  <button @click="countPush(item)">&#43;</button>
                </div>
                <button @click="deleteItem(item.id)" class="remove-button">删除</button>
              </div>
              <div class="item-price">加總: {{ item.total }}</div>
            </div>
          </div>
        </div>
        <div class="cart-summary">
          <div class="summary-item">
            <span>總價 : {{ totalPrice }}</span>
            <button class="summaryBtn" @click="nextStep">填寫訂單資料</button>
          </div>
        </div>
      </div>
    </div>
    <div class="consumerGroup" v-if="currentStep == 2">
      <h3>填寫訂單資料</h3>
      <Form v-slot="{ errors, values, validate }" @submit="checkout">
        <div class="mb-3">
          <label for="name" class="form-label">姓名</label>

          <Field
            id="name"
            name="name"
            type="text"
            class="form-control"
            placeholder="請輸入姓名"
            rules="required"
            v-model="form.user.name"
          ></Field>

          <error-message name="name" class="invalid-feedback"></error-message>
        </div>

        <div class="mb-3">
          <label for="email" class="form-label">Email</label>

          <Field
            id="email"
            name="email"
            type="email"
            class="form-control"
            :class="{ 'is-invalid': errors['email'] }"
            placeholder="請輸入 Email"
            rules="email|required"
            v-model="form.user.email"
          ></Field>

          <error-message name="email" class="invalid-feedback"></error-message>
        </div>

        <div class="mb-3">
          <label for="phone" class="form-label">電話</label>

          <Field
            id="phone"
            name="phone"
            type="text"
            class="form-control"
            placeholder="請輸入電話"
            rules="required"
            v-model="form.user.tel"
          ></Field>

          <error-message name="phone" class="invalid-feedback"></error-message>
        </div>

        <div class="mb-3">
          <label for="address" class="form-label">地址</label>

          <Field
            id="address"
            name="address"
            type="text"
            class="form-control"
            placeholder="請輸入地址"
            rules="required"
            v-model="form.user.address"
          ></Field>

          <error-message name="name" class="invalid-feedback"></error-message>
        </div>
        <label for="message" class="form-label">留言</label>
        <textarea
          class="form-control"
          v-bind="form.user.message"
          id="floatingTextarea2"
          style="height: 100px"
          name="message"
        ></textarea>

        <div class="buttonGroup">
          <button class="btn btn-primary mt-3" type="submit" @click="checkout()">
            確定
          </button>
        </div>
      </Form>
    </div>
    <div class="checkOrderGroup" v-if="currentStep == 3">
      <form class="col-md-6">
        <table class="table align-middle">
          <thead>
            <th>品名</th>
            <th>數量</th>
            <th>單價</th>
          </thead>
          <tbody>
            <tr v-for="(item, index) in orderData.products" :key="index">
              <td>{{ item.product.title }}</td>
              <td>{{ item.qty }}/{{ item.product.unit }}</td>
              <td class="text-end">{{ item.final_total }} 元</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="2" class="text-end">總計</td>
              <td class="text-end">{{ orderData.total }} 元</td>
            </tr>
          </tfoot>
        </table>

        <table class="table">
          <tbody>
            <tr>
              <th width="100">Email</th>
              <td>{{ orderData.user?.email || "N/A" }}</td>
            </tr>
            <tr>
              <th>姓名</th>
              <td>{{ orderData.user?.name || "N/A" }}</td>
            </tr>
            <tr>
              <th>收件人電話</th>
              <td>{{ orderData.user?.tel || "N/A" }}</td>
            </tr>
            <tr>
              <th>收件人地址</th>
              <td>{{ orderData.user?.address || "N/A" }}</td>
            </tr>
            <tr>
              <th>付款狀態</th>
              <td>
                <span v-if="orderData.is_paid" class="text-success">付款完成</span>
                <span v-else class="text-danger">尚未付款</span>
              </td>
            </tr>
          </tbody>
        </table>
        <div>
          <button class="btn btn-danger disabled">確認付款去</button>
        </div>
      </form>
    </div>
  </div>
  <Footer></Footer>
</template>

