<script>
import { ref, computed } from "vue";
import Navbar from "@/components/Navbar.vue";
import ToastList from "@/components/ToastList.vue";
import Footer from "@/components/Footer.vue";
export default {
  data() {
    return {
      currentStep: 1,
      steps: [
        { id: 1, name: "購物車" },
        { id: 2, name: "填寫顧客資料" },
        { id: 3, name: "確認訂單" },
        { id: 4, name: "結帳" },
      ],
      cartItemsGroup: {},
      cartItems: [],
      totalPrice: 0,
      isLoading: false,
      form: {
        user: {
          name: "",
          email: "",
          tel: "",
          address: "",
        },
        message: "",
      },
      orderId: "",
      orderData: "",
    };
  },
  components: {
    Navbar,
    Footer
  },
  methods: {
    countBack(item) {
      if (item.qty <= 1) return;

      const api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_API_NAME}/cart/${item.id}`;
      const cart = {
        product_id: item.product_id,
        qty: item.qty - 1,
      };
      this.$http.put(api, { data: cart }).then((res) => {
        console.log(res);
        this.getCart();
      });
    },
    countPush(item) {
      if (item.qty > 99) {
        alert("數量過多");
        this.getCart();
        return;
      }
      const api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_API_NAME}/cart/${item.id}`;
      const cart = {
        product_id: item.product_id,
        qty: item.qty + 1,
      };
      this.$http.put(api, { data: cart }).then((res) => {
        console.log(res);
        this.getCart();
      });
    },
    getCart() {
      this.isLoading = true;
      const api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_API_NAME}/cart`;
      this.$http.get(api).then((res) => {
        if (res.data.success) {
          console.log(res.data);

          this.cartItemsGroup = res.data;
          this.cartItems = res.data.data.carts;
          this.totalPrice = res.data.data.final_total;
        }
        this.isLoading = false;
      });
    },
    changeCount(item) {
      if (item.qty <= 0) {
        this.getCart();
        return;
      } else if (item.qty > 99) {
        alert("數量過多");
        this.getCart();
        return;
      }
      const api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_API_NAME}/cart/${item.id}`;
      const cart = {
        product_id: item.product_id,
        qty: item.qty,
      };
      this.$http.put(api, { data: cart }).then((res) => {
        console.log(res);
        this.getCart();
      });
    },
    deleteItem(id) {
      const api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_API_NAME}/cart/${id}`;
      this.$http.delete(api).then((res) => {
        this.getCart();
      });
    },
    nextStep() {
      if (this.currentStep < this.steps.length) {
        this.currentStep++;
      }
    },
    prevStep() {
      if (this.currentStep > 1) {
        this.currentStep--;
      }
    },
    isPhone(value) {
      const phoneNumber = /^(09)[0-9]{8}$/;
      return phoneNumber.test(value) ? true : "需要正確的電話號碼";
    },
    checkout() {
      this.isLoading = true
      const api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_API_NAME}/order`;
      this.$http
        .post(api, { data: { user: this.form.user, message: this.form.user.message } })
        .then((res) => {
        
          this.orderId = res.data.orderId;
          this.orderCheck();
        });
      this.nextStep();
    },
    orderCheck() {
      const api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_API_NAME}/order/${this.orderId}`;
      this.$http.get(api).then((res) => {
        console.log(res);
        this.orderData = res.data.order;
        this.isLoading = false
      });
    },
  },
  created() {
    this.getCart();
  },
};
</script>
